
.carcontrol {
  font-size: 0;
}
.carcontrol .move-enter-active, .carcontrol .move-leave-active {
    opacity: 1;
    -webkit-transform: translate3D(0, 0, 0);
            transform: translate3D(0, 0, 0);
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
}
.carcontrol .move-enter-active .inner, .carcontrol .move-leave-active .inner {
      display: inline-block;
      line-height: 24px;
      font-size: 24px;
      color: #00a0dc;
      -webkit-transition: all 0.4s linear;
      transition: all 0.4s linear;
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
.carcontrol .move-enter, .carcontrol .move-leave-active {
    opacity: 0;
    -webkit-transform: translate3D(24px, 0, 0);
            transform: translate3D(24px, 0, 0);
}
.carcontrol .move-enter .inner, .carcontrol .move-leave-active .inner {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
}
.carcontrol .cart-decrease {
    display: inline-block;
    padding: 0 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}
.carcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}
.carcontrol .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}



