
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #ffffff;
  -webkit-transition: all .2s linear;
  transition: all .2s linear;
}
.food.move-leave, .food.move-enter-active {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.food.move-enter, .food.move-leave-active {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.food .food-content .image-header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.food .food-content .image-header img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.food .food-content .image-header .back {
      position: absolute;
      top: 10px;
      left: 10px;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.2);
}
.food .food-content .image-header .back .icon-arrow_lift {
        display: block;
        padding: 10px;
        font-size: 20px;
        color: #fff;
}
.food .food-content .content {
    position: relative;
    padding: 18px;
}
.food .food-content .content .title {
      line-height: 14px;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .food-content .content .detail {
      margin-bottom: 18px;
      line-height: 10px;
      height: 10px;
      font-size: 0;
}
.food .food-content .content .detail .sell-count, .food .food-content .content .detail .rating {
        font-size: 10px;
        color: #93999f;
}
.food .food-content .content .detail .sell-count {
        margin-right: 12px;
}
.food .food-content .content .price {
      font-weight: 700;
      line-height: 24px;
}
.food .food-content .content .price .now {
        margin-right: 8px;
        font-size: 14px;
        color: #f01414;
}
.food .food-content .content .price .old {
        text-decoration: line-through;
        font-size: 10px;
        color: #93999f;
}
.food .food-content .content .cartcontrol-wrapper {
      position: absolute;
      right: 12px;
      bottom: 12px;
}
.food .food-content .content .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      height: 24px;
      line-height: 24px;
      padding: 0 12px;
      box-sizing: border-box;
      border-radius: 12px;
      font-size: 10px;
      color: #fff;
      background: #00a0dc;
      -webkit-transition: all .2s;
      transition: all .2s;
}
.food .food-content .content .buy.fade-leave, .food .food-content .content .buy.fade-enter-active {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
}
.food .food-content .content .buy.fade-enter, .food .food-content .content .buy.fade-leave-active {
        -webkit-transform: translate3d(-100%, 0, 0);
                transform: translate3d(-100%, 0, 0);
        opacity: 0;
}
.food .food-content .info {
    padding: 18px;
}
.food .food-content .info .title {
      line-height: 17px;
      margin-bottom: 6px;
      font-size: 14px;
      color: #07111b;
}
.food .food-content .info .text {
      line-height: 24px;
      padding: 0 8px;
      font-size: 12px;
      font-weight: 100;
      color: #4d555d;
}
.food .food-content .rating {
    padding-top: 18px;
}
.food .food-content .rating .title {
      line-height: 14px;
      margin-left: 18px;
      font-size: 14px;
      color: #07111b;
}
.food .food-content .rating .rating-wrapper {
      padding: 0 18px;
}
.food .food-content .rating .rating-wrapper .rating-item {
        position: relative;
        padding: 16px 0;
        position: relative;
}
.food .food-content .rating .rating-wrapper .rating-item:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: ' ';
}
.food .food-content .rating .rating-wrapper .rating-item .user {
          position: absolute;
          right: 0;
          top: 16px;
          line-height: 12px;
          font-size: 0;
}
.food .food-content .rating .rating-wrapper .rating-item .user .name {
            display: inline-block;
            margin-right: 6px;
            vertical-align: top;
            font-size: 10px;
            color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .user .avatar {
            border-radius: 50%;
}
.food .food-content .rating .rating-wrapper .rating-item .time {
          margin-bottom: 6px;
          line-height: 12px;
          font-size: 10px;
          color: #93999f;
}
.food .food-content .rating .rating-wrapper .rating-item .text {
          line-height: 16px;
          font-size: 12px;
          color: #07111b;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up, .food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            margin-right: 4px;
            line-height: 16px;
            font-size: 12px;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_up {
            color: #00a0dc;
}
.food .food-content .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            color: #93999f;
}
.food .food-content .rating .rating-wrapper .no-rating {
        padding: 16px 0;
        font-size: 12px;
        color: #93999f;
}



